<template>
    <div id="user_profile">
        <van-nav-bar
        title="个人主页"
        left-text="返回"
        left-arrow
        @click-left="$router.go(-1)"
        />
      <div class="content">
        <user-profile :user="userData"/>
        <div><span>{{ userData.username }}的作品</span></div>
        <div class="my-aritcle">
          <span v-for="article in myArticles" :key="article.articleId">
            <article-card :article="article" :show-edit="false" />
          </span>
        </div>
        <div>
        </div>
      </div>
      <div class="blank-block"></div>
    </div>
  </template>
<script>
import { getUserId } from '@/util/local'
import UserProfile from '@/components/UserProfile.vue'
import articleCard from '@/components/articleCard.vue'
import { getArticleByAuthorId } from '@/api/article'
import { getUserById } from '@/api/user'
export default {
  name: 'user_profile',
  components: {
    UserProfile,
    articleCard
  },
  data: () => {
    return {
      isLoggedin: false,
      userData: {},
      myArticles: []
    }
  },
  methods: {
  },
  async created () {
    console.log(getUserId(), 'type:', typeof getUserId())
    // if (getUserId() === '') {
    //   return
    // }
    this.isLoggedin = true
    const res = await getUserById(this.$route.query.userId)
    this.userData = res.data
    console.log('res', res)
    const articles = await getArticleByAuthorId(this.$route.query.userId)
    console.log('my articles:', articles)
    if (articles.code === 200) {
      this.myArticles = articles.data
    }
  }
}
</script>
  <style scoped>
  .blank-block{
    height: 200px;
    /* background-color: red; */
  }
  </style>
